import { articlesDetails } from "@/api/article";
import favorite from "./components/index";
export default {
  name: "articleIndex",
  async asyncData({ params }) {
    console.log(params);
    const info = await articlesDetails(params.slug);
    const { article } = info.data;
    article.favoritDisable = false;
    return {
      article,
    };
  },
  components: {
    favorite,
  },
};
